import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnSave } from 'components/common/button';
import { Card, Row, Table, Col } from 'antd';
import { nhangiayto, papers } from 'dump/data';
import FormSearchStudent from './FormSearchStudent';
import FormInfoReceive from './FormInfoReceive';

const columns = [
  {
    title: 'S bo danh',
    dataIndex: 'sbd',
    key: 'code',
  },
  {
    title: 'H tn',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngy sinh',
    dataIndex: 'birthdate',
    key: 'name',
  },
  {
    title: 'Gii tnh',
    dataIndex: 'sex',
    key: 'name',
  },
  {
    title: 'Tng im',
    dataIndex: 'total',
    key: 'name',
  },
  {
    title: 'Ngnh',
    dataIndex: 'nganh',
    key: 'name',
  },
  {
    title: 'Ngy nhp hc',
    dataIndex: 'ngaynh',
    key: 'name',
  },
  {
    title: 'Ngi tip nhn',
    dataIndex: 'nguoitn',
    key: 'name',
  },
  {
    title: 'Ngi thu tin',
    dataIndex: 'nguoithutien',
    key: 'name',
  },
  {
    title: 'S tin np',
    dataIndex: 'sotiennop',
    key: 'name',
  },
];

const rowSelection = {
  onChange: (selectedRowKeys, selectedRows) => {
    console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
  },
  getCheckboxProps: record => ({
    disabled: record.name === 'Disabled User', // Column configuration not to be checked
    name: record.name,
  }),
};

const paperColumns = [
  {
    title: 'Bt buc',
    dataIndex: 'isRequire',
    key: 'code',
  },
  {
    title: 'Tn giy t',
    dataIndex: 'name',
    key: 'code',
  },
  {
    title: 'S lng',
    dataIndex: 'number',
    key: 'code',
  },
];

class ReceiveAdmissionPapersPage extends Component {
  state = {};

  render() {
    return (
      <Fragment>
        <Card title="Nhn giy t nhp hc">
          <Row>
            <Col span={24}>
              <FormSearchStudent />
            </Col>
            <Col span={24}>
              <Table
                rowSelection={rowSelection}
                bordered
                dataSource={nhangiayto}
                columns={columns}
              />
            </Col>
          </Row>
        </Card>
        <Card title="Thng tin giy t">
          <Row>
            <Col span={8}>
              <FormInfoReceive />
            </Col>
            <Col span={16}>
              <Row>
                <Table
                  rowSelection={rowSelection}
                  bordered
                  dataSource={papers}
                  columns={paperColumns}
                />
              </Row>
            </Col>
            <Col offset={8} span={12}>
              <BtnSave htmlType="submit" />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ReceiveAdmissionPapersPage />
  </Page>
);



