import React, { Component } from 'react';
import { Col, Form, Input, DatePicker, Checkbox } from 'antd';

@Form.create({ name: 'FormInfoReceive' })
class FormInfoReceive extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Col span={24}>
          <Form.Item label="Ngy nhp hc">
            {getFieldDecorator('nnhh', {})(<DatePicker />)}
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="Ngi tip nhn">{getFieldDecorator('ntn', {})(<Input />)}</Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="H NH">{getFieldDecorator('hnh', {})(<Input />)}</Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="Trnh ">{getFieldDecorator('td', {})(<Input />)}</Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="Khi thi">{getFieldDecorator('kt', {})(<Input />)}</Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="Ngnh o to">{getFieldDecorator('nt', {})(<Input />)}</Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label=" ">
            {getFieldDecorator('plkh', {})(
              <Checkbox value={1}>Phn lp khi gi nhp hc</Checkbox>
            )}
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label=" ">
            {getFieldDecorator('inp', {})(<Checkbox value={2}>In phiu khi lu</Checkbox>)}
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormInfoReceive;



